<!-- ,, -->
<template>
  <div class="second-minute-hour-box">
    <el-radio-group v-model="type" size="mini" @change="getValue">
      <!-- * -->
      <div class="box-bottom">
        <el-radio label="1" size="mini" border>{{lable}}</el-radio>
      </div>
      <!--  -->
      <div class="box-bottom cycle">
        <el-radio label="2" size="mini" border></el-radio>
        <span style="margin-right:8px;"></span>
        <el-input-number
          @change="getChangeTime"
          v-model="cycle.start"
          :min="1"
          :max="maxTime - 1"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin:0 8px;"></span>
        <el-input-number
          @change="getChangeTime"
          v-model="cycle.end"
          :min="parseInt(cycle.start) + 1"
          :max="maxTime"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left:8px;">{{lable}}</span>
      </div>
      <!--  -->
      <div class="box-bottom cycle">
        <el-radio label="3" size="mini" border></el-radio>
        <span style="margin-right:8px;"></span>
        <el-input-number
          @change="getLoopTime"
          v-model="loop.start"
          :min="0"
          :max="maxTime"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin:0 8px;">{{lable}}</span>
        <el-input-number
          @change="getLoopTime"
          v-model="loop.end"
          :min="1"
          :max="maxTime"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left:8px;">{{lable}}</span>
      </div>
      <!--  -->
      <div class="box-bottom">
        <el-radio label="4" size="mini" border></el-radio>
      </div>
      <div>
        <el-checkbox-group v-model="appoint" class="second-minute-chekck-group">
          <el-checkbox
            @change="getAppointTime"
            v-for="i in appointNumber"
            :key="i-1"
            :label="i-1"
          >{{i-1}}</el-checkbox>
        </el-checkbox-group>
      </div>
    </el-radio-group>
  </div>
</template>

<script>
export default {
  props: {
    // lable(),,
    lable: {
      type: String,
      requied: true
    }
  },
  data() {
    return {
      // lable,60,24
      appointNumber: this.lable == "" ? 24 : 60,
      // 
      maxTime: this.lable == "" ? 23 : 59,
      // 
      type: "4",
      // 
      cycle: {
        start: 0,
        end: 0
      },
      // 
      loop: {
        start: 0,
        end: 0
      },
      appoint: [0], // 
      result: ""
    };
  },
  methods: {
    // 
    getValue(value) {
      // 
      this.appoint = [];
      if (value == "1") {
        // *
        this.result = "*";
      } else if (value == "2") {
        // 
        this.result = this.cycle.start + "-" + this.cycle.end;
      } else if (value == "3") {
        // 
        this.result = this.loop.start + "/" + this.loop.end;
      } else {
        // -0
        this.appoint = [0];
        this.result = this.appoint.join(",");
      }
      console.log("result", this.result);
      this.$emit("time", this.result);
    },
    // 
    getChangeTime() {
      this.type = "2";
      this.result = this.cycle.start + "-" + this.cycle.end;
      console.log("result", this.result);
      this.$emit("time", this.result);
      // 
      this.appoint = [];
    },
    // 
    getLoopTime() {
      this.type = "3";
      this.result = this.loop.start + "/" + this.loop.end;
      console.log("result", this.result);
      this.$emit("time", this.result);
      // 
      this.appoint = [];
    },
    // 
    getAppointTime() {
      this.type = "4";
      this.result = this.appoint
        .sort((a, b) => {
          return a - b;
        })
        .join(",");
      console.log("result", this.result);
      this.$emit("time", this.result);
    }
  }
};
</script>

<style lang="css">
.second-minute-hour-box {
  font-size: 12px;
}
.second-minute-hour-box .box-bottom {
  margin-bottom: 8px;
}
.second-minute-hour-box .cycle {
  font-size: 12px;
}
.second-minute-hour-box .second-minute-chekck-group {
  display: flex;
  flex-wrap: wrap;
}
.second-minute-hour-box .el-checkbox + .el-checkbox {
  margin: 0;
}
.second-minute-hour-box .second-minute-chekck-group .el-checkbox {
  margin: 0;
  width: 10%;
}
</style>



