<template>
    <div>
        <h2 class="red">SingleFileConponent...{{msg}}:{{count}}</h2>
        <button v-on:click="vOnClickTest()"></button>
    </div>
</template>

<script>
export default {
  name: 'SingleFileConponent',
  props: {
    msg: {
      type: String,
      default: 'SingleFileConponent msg'
    },
    //  (`null`  `undefined` )
    propA: Number,
    // 
    propB: [String, Number],
    // 
    propC: {
      type: String,
      required: true
    },
    // 
    propD: {
      type: Number,
      default: 100
    },
    // 
    propE: {
      type: Object,
      // 
      default: function () {
        return { message: 'hello' }
      }
    },
    // 
    propF: {
      validator: function (value) {
        // 
        return ['success', 'warning', 'danger'].indexOf(value) !== -1
      }
    }
  },
  methods: {
    vOnClickTest: function () {
      this.count++
      this.$emit('clicknow', this.count) // ,
    }
  },
  data () {
    return {
      // propsdata
      count: 0
    }
  },
  computed: {
    // propscomputed
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.red{color: #FF0000;}
</style>



