<template>
  <!-- ?  -->
  <div class="day-and-week-box">
    <el-radio-group v-model="type" size="mini" @change="getValue">
      <!--  -->
      <div class="box-bottom">
        <el-radio label="1" size="mini" border></el-radio>
      </div>
      <div class="box-bottom cycle">
        <el-radio label="2" size="mini" border></el-radio>
        <span style="margin-right: 8px;"></span>
        <el-input-number
          @change="getChangeDay"
          v-model="dayCycle.start"
          :min="1"
          :max="31"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left: 8px; margin-right: 8px;"></span>
        <el-input-number
          @change="getChangeDay"
          v-model="dayCycle.end"
          :min="2"
          :max="31"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left: 8px;"></span>
      </div>
      <div class="box-bottom cycle">
        <el-radio label="3" size="mini" border></el-radio>
        <span style="margin-right: 8px;"></span>
        <el-input-number
          @change="getLoopDay"
          v-model="dayLoop.start"
          :min="1"
          :max="31"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left: 8px; margin-right: 8px;"></span>
        <el-input-number
          @change="getLoopDay"
          v-model="dayLoop.end"
          :min="1"
          :max="31"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left: 8px;"></span>
      </div>
      <div class="box-bottom cycle">
        <el-radio label="5" size="mini" border></el-radio>
        <span style="margin-right: 8px;"></span>
        <el-input-number
          @change="getworkDay"
          v-model="work"
          :min="1"
          :max="31"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left: 8px;"></span>
      </div>
      <div class="box-bottom">
        <el-radio label="6" size="mini" border></el-radio>
      </div>
      <div class="box-bottom">
        <el-radio label="7" size="mini" border></el-radio>
      </div>
      <!--  -->
      <div class="box-border"></div>
      <div class="box-bottom">
        <el-radio label="11" size="mini" border></el-radio>
      </div>
      <div class="box-bottom cycle">
        <el-radio label="12" size="mini" border></el-radio>
        <span style="margin-right: 8px;">({{weekFormat[weekCycle.start]}})</span>
        <el-input-number
          @change="getChangeWeek"
          v-model="weekCycle.start"
          :min="1"
          :max="7"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left: 8px; margin-right: 8px;">({{weekFormat[weekCycle.end]}})</span>
        <el-input-number
          @change="getChangeWeek"
          v-model="weekCycle.end"
          :min="2"
          :max="7"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
      </div>
      <div class="box-bottom cycle">
        <el-radio label="13" size="mini" border></el-radio>
        <span style="margin-right: 8px;"></span>
        <el-input-number
          @change="getLoopWeek"
          v-model="weekLoop.start"
          :min="1"
          :max="4"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left: 8px; margin-right: 8px;">({{weekFormat[weekLoop.end]}})</span>
        <el-input-number
          @change="getLoopWeek"
          v-model="weekLoop.end"
          :min="1"
          :max="7"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
        <span style="margin-left: 8px;"></span>
      </div>
      <div class="box-bottom cycle">
        <el-radio label="15" size="mini" border></el-radio>
        <span style="margin-right: 8px;">({{weekFormat[last]}})</span>
        <el-input-number
          @change="getlastWeek"
          v-model="last"
          :min="1"
          :max="7"
          size="mini"
          style="width: 100px;"
        ></el-input-number>
      </div>
      <div class="box-border"></div>
      <!-- -->
      <div class="box-bottom">
        <el-radio label="4" size="mini" border></el-radio>
        <el-radio label="14" size="mini" border></el-radio>
      </div>
      <div>
        <el-checkbox-group
          v-model="appointDay"
          v-show="type == 4"
          class="second-minute-chekck-group"
        >
          <el-checkbox @change="getAppointDay" v-for="j in 31" :key="j" :label="j"></el-checkbox>
        </el-checkbox-group>
        <el-checkbox-group
          v-model="appointWeek"
          v-show="type == 14"
          class="second-minute-chekck-group"
        >
          <el-checkbox @change="getAppointWeek" v-for="j in 7" :key="j" :label="j"></el-checkbox>
        </el-checkbox-group>
      </div>
    </el-radio-group>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      type: "1",
      // -
      dayCycle: {
        start: 0,
        end: 0
      },
      // -
      dayLoop: {
        start: 0,
        end: 0
      },
      // -
      appointDay: [],
      // -
      work: 1,
      // -
      weekCycle: {
        start: 0,
        end: 0
      },
      // -
      weekLoop: {
        start: 0,
        end: 0
      },
      // -
      last: 1,
      // -
      appointWeek: [],
      // -
      weekFormat: {
        1: "",
        2: "",
        3: "",
        4: "",
        5: "",
        6: "",
        7: ""
      },
      // 
      result: {
        day: "",
        week: ""
      }
    };
  },
  methods: {
    // 
    getValue(value) {
      // 
      this.appointWeek = [];
      this.appointDay = [];
      if (value == "1") {
        // 
        this.result.day = "*";
        // 
        this.result.week = "?";
      } else if (value == "2") {
        // -
        this.result.day = this.dayCycle.start + "-" + this.dayCycle.end;
        // 
        this.result.week = "?";
      } else if (value == "3") {
        // -
        this.result.day = this.dayLoop.start + "/" + this.dayLoop.end;
        // 
        this.result.week = "?";
      } else if (value == "5") {
        // 
        this.result.day = this.work + "W";
        // 
        this.result.week = "?";
      } else if (value == "6") {
        // 
        this.result.day = "L";
        // 
        this.result.week = "?";
      } else if (value == "7") {
        // 
        this.result.day = "LW";
        // 
        this.result.week = "?";
      } else if (value == "4") {
        // -0
        this.appointDay = [1];
        this.result.day = this.appointDay.join(",");
        // 
        this.result.week = "?";
      } else if (value == "11") {
        // 
        this.result.week = "*";
        // 
        this.result.day = "?";
      } else if (value == "12") {
        // -
        this.result.week = this.weekCycle.start + "-" + this.weekCycle.end;
        // 
        this.result.day = "?";
      } else if (value == "13") {
        // -
        this.result.week = this.weekLoop.end + "#" + this.weekLoop.start;
        // 
        this.result.day = "?";
      } else if (value == "15") {
        // -
        this.result.week = this.last + "L";
        // 
        this.result.day = "?";
      } else if (value == "14") {
        // -0
        this.appointWeek = [1];
        this.result.week = this.appointWeek.join(",");
        // 
        this.result.day = "?";
      } else {
        console.log('none');
      }
      this.$emit("dayAndWeek", this.result);
    },
    // -
    getChangeDay() {
      this.type = "2";
      this.result.day = this.dayCycle.start + "-" + this.dayCycle.end;
      this.result.week = "?";
      this.$emit("dayAndWeek", this.result);
      // 
      this.appointWeek = [];
      this.appointDay = [];
    },
    // -
    getLoopDay() {
      this.type = "3";
      this.result.day = this.dayLoop.start + "/" + this.dayLoop.end;
      this.result.week = "?";
      this.$emit("dayAndWeek", this.result);
      // 
      this.appointWeek = [];
      this.appointDay = [];
    },
    // -
    getworkDay() {
      this.type = "5";
      this.result.day = this.work + "W";
      this.result.week = "?";
      this.$emit("dayAndWeek", this.result);
      // 
      this.appointWeek = [];
      this.appointDay = [];
    },
    // -
    getAppointDay() {
      this.type = "4";
      this.appointWeek = [];
      this.result.day = this.appointDay
        .sort((a, b) => {
          return a - b;
        })
        .join(",");
      this.result.week = "?";
      this.$emit("dayAndWeek", this.result);
    },
    // -
    getAppointWeek() {
      this.type = "14";
      this.appointDay = [];
      this.result.week = this.appointWeek
        .sort((a, b) => {
          return a - b;
        })
        .join(",");
      this.result.day = "?";
      this.$emit("dayAndWeek", this.result);
    },
    // - 
    getChangeWeek() {
      this.type = "12";
      this.result.week = this.weekCycle.start + "-" + this.weekCycle.end;
      this.result.day = "?";
      this.$emit("dayAndWeek", this.result);
      // 
      this.appointWeek = [];
      this.appointDay = [];
    },
    // - -
    getLoopWeek() {
      this.type = "13";
      this.result.week = this.weekLoop.end + "#" + this.weekLoop.start;
      this.result.day = "?";
      this.$emit("dayAndWeek", this.result);
      // 
      this.appointWeek = [];
      this.appointDay = [];
    },
    // -
    getlastWeek() {
      this.type = "15";
      this.result.week = this.last + "L";
      this.result.day = "?";
      this.$emit("dayAndWeek", this.result);
      // 
      this.appointWeek = [];
      this.appointDay = [];
    }
  }
};
</script>

<style lang="css">
.day-and-week-box {
  font-size: 12px;
}
.day-and-week-box .box-border {
  height: 0;
  overflow: hidden;
  border-top: 1px dashed #ebebeb;
  margin-bottom: 8px;
  width: 100%;
}
.day-and-week-box .box-bottom {
  margin-bottom: 8px;
}
.day-and-week-box .cycle {
  font-size: 12px;
}
.day-and-week-box .second-minute-chekck-group {
  display: flex;
  flex-wrap: wrap;
}
.day-and-week-box .el-checkbox + .el-checkbox {
  margin: 0;
}
.day-and-week-box .second-minute-chekck-group .el-checkbox {
  margin: 0;
  width: 10%;
}
</style>



