<template>
  <!--  -->
  <div class="cron-default-first">
    <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="" name="day">
        <el-radio-group v-model="type" size="mini" @change="getValue">
          <div class="box-bottom">
            <el-radio label="1" size="mini" border>0</el-radio>
          </div>
          <div class="box-bottom cycle">
            <el-radio label="2" size="mini" border></el-radio>
            <div style="margin-top:8px;">
              <span style="margin-right:8px;"></span>
              <el-input-number
                @change="getChangeDayHourTime"
                v-model="day.hour"
                :min="0"
                :max="23"
                size="mini"
                style="width: 100px;"
              ></el-input-number>
              <span style="margin:0 8px;"></span>
              <el-input-number
                @change="getChangeDayHourTime"
                v-model="day.minute"
                :min="0"
                :max="59"
                size="mini"
                style="width: 100px;"
              ></el-input-number>
              <span style="margin-left:8px;"></span>
            </div>
          </div>
        </el-radio-group>
      </el-tab-pane>
      <el-tab-pane label="" name="week">
        <el-radio-group v-model="type" size="mini" @change="getValue">
          <div class="box-bottom">
            <el-radio label="11" size="mini" border>0</el-radio>
          </div>
          <div class="box-bottom">
            <el-radio label="12" size="mini" border
              >0</el-radio
            >
          </div>
          <div class="box-bottom">
            <el-radio label="13" size="mini" border>0</el-radio>
          </div>
          <div class="box-bottom cycle">
            <el-radio label="14" size="mini" border
              ></el-radio
            >
            <div style="margin-top:8px;">
              <span style="margin-right:8px;"></span>
              <el-select
                v-model="weekDay.week"
                placeholder=""
                style="width:100px;"
                size="mini"
                @change="getChangeWeekTime"
              >
                <el-option label="" :value="'1'"></el-option>
                <el-option label="" :value="'2'"></el-option>
                <el-option label="" :value="'3'"></el-option>
                <el-option label="" :value="'4'"></el-option>
                <el-option label="" :value="'5'"></el-option>
                <el-option label="" :value="'6'"></el-option>
                <el-option label="" :value="'7'"></el-option>
              </el-select>
              <el-input-number
                @change="getChangeWeekTime"
                v-model="weekDay.hour"
                :min="0"
                :max="23"
                size="mini"
                style="width: 100px;"
              ></el-input-number>
              <span style="margin:0 8px;"></span>
              <el-input-number
                @change="getChangeWeekTime"
                v-model="weekDay.minute"
                :min="0"
                :max="59"
                size="mini"
                style="width: 100px;"
              ></el-input-number>
              <span style="margin-left:8px;"></span>
            </div>
          </div>
        </el-radio-group>
      </el-tab-pane>
      <el-tab-pane label="" name="month">
        <el-radio-group v-model="type" size="mini" @change="getValue">
          <div class="box-bottom">
            <el-radio label="21" size="mini" border>10</el-radio>
          </div>
          <div class="box-bottom">
            <el-radio label="22" size="mini" border>150</el-radio>
          </div>
          <div class="box-bottom cycle">
            <el-radio label="23" size="mini" border
              ></el-radio
            >
            <div style="margin-top:8px;">
              <span style="margin-right:8px;"></span>
              <el-input-number
                @change="getChangeMonthTime"
                v-model="month.day"
                :min="1"
                :max="31"
                size="mini"
                style="width: 100px;"
              ></el-input-number>
              <span style="margin:0 8px;">()</span>
              <el-input-number
                @change="getChangeMonthTime"
                v-model="month.hour"
                :min="0"
                :max="23"
                size="mini"
                style="width: 100px;"
              ></el-input-number>
              <span style="margin:0 8px;"></span>
              <el-input-number
                @change="getChangeMonthTime"
                v-model="month.minute"
                :min="0"
                :max="59"
                size="mini"
                style="width: 100px;"
              ></el-input-number>
              <span style="margin-left:8px;"></span>
            </div>
          </div>
        </el-radio-group>
      </el-tab-pane>
    </el-tabs>
    <div class="table-box">
      <span>{{ cronExpression }}</span>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeName: "day",
      // 
      type: "1",
      // 
      day: {
        hour: "0",
        minute: "0"
      },
      //  
      weekDay: {
        week: "2",
        hour: "0",
        minute: "0"
      },
      // 
      month: {
        day: "1",
        hour: "0",
        minute: "0"
      },
      // 
      cronExpression: "0 0 0 * * ?"
    };
  },
  methods: {
    resetValue() {
      this.day = {
        hour: "0",
        minute: "0"
      };
      this.weekDay = {
        week: "2",
        hour: "0",
        minute: "0"
      };
      this.month = {
        day: "1",
        hour: "0",
        minute: "0"
      };
    },
    // 
    getValue() {
      this.resetValue();
      if (this.type == "1") {
        // 0
        this.cronExpression = "0 0 0 * * ?";
      } else if (this.type == "2") {
        // **
        this.cronExpression =
          "0 " + this.day.minute + " " + this.day.hour + " * * ?";
      } else if (this.type == "11") {
        // 0
        this.cronExpression = "0 0 0 ? * 2";
      } else if (this.type == "12") {
        // 0
        this.cronExpression = "0 0 0 ? * 2-6";
      } else if (this.type == "13") {
        // 0
        this.cronExpression = "0 0 0 ? * 1,7";
      } else if (this.type == "14") {
        //  
        this.cronExpression =
          "0 " +
          this.weekDay.minute +
          " " +
          this.weekDay.hour +
          " ? * " +
          this.weekDay.week;
      } else if (this.type == "21") {
        // 10
        this.cronExpression = "0 0 0 1 * ?";
      } else if (this.type == "22") {
        // 150
        this.cronExpression = "0 0 0 15 * ?";
      } else if (this.type == "23") {
        // 
        this.cronExpression =
          "0 " +
          this.month.minute +
          " " +
          this.month.hour +
          " " +
          this.month.day +
          " * ?";
      }
    },
    // 
    getChangeDayHourTime() {
      this.type = "2";
      this.cronExpression =
        "0 " + this.day.minute + " " + this.day.hour + " * * ?";
    },
    //  
    getChangeWeekTime() {
      this.type = "14";
      this.cronExpression =
        "0 " +
        this.weekDay.minute +
        " " +
        this.weekDay.hour +
        " ? * " +
        this.weekDay.week;
    },
    // 
    getChangeMonthTime() {
      this.type = "23";
      this.cronExpression =
        "0 " +
        this.month.minute +
        " " +
        this.month.hour +
        " " +
        this.month.day +
        " * ?";
    },
    // tab
    handleClick() {
      if (this.activeName == "day") {
        this.type = "1";
        this.getValue();
      } else if (this.activeName == "week") {
        this.type = "11";
        this.getValue();
      } else {
        this.type = "21";
        this.getValue();
      }
    }
  }
};
</script>
<style lang="css">
.cron-default-first .box-bottom {
  margin-bottom: 8px;
}
.cron-default-first .cycle {
  font-size: 12px;
}
.cron-default-first .el-tabs .el-tabs__content {
  overflow-x: hidden;
  overflow-y: auto;
  height: 220px;
}
.cron-default-first .table-box {
  border-top: 1px solid #ebebeb;
  margin-top: 10px;
  padding-top: 10px;
  min-width: 400px;
}
</style>


