<template>
  <div class="cron-box">
    <el-tabs v-model="activeName" type="border-card" stretch>
      <el-tab-pane label="" name="s">
        <Time lable="" @time="second"></Time>
      </el-tab-pane>
      <el-tab-pane label="" name="m">
        <Time lable="" @time="minute"></Time>
      </el-tab-pane>
      <el-tab-pane label="" name="h">
        <Time lable="" @time="hour"></Time>
      </el-tab-pane>
      <el-tab-pane label="()" name="d">
        <day lable="" @dayAndWeek="dayAndWeek"></day>
      </el-tab-pane>
      <el-tab-pane label="" name="month">
        <month lable="" @month="month"></month>
      </el-tab-pane>
      <el-tab-pane label="" name="year">
        <year lable="" @year="year"></year>
      </el-tab-pane>
    </el-tabs>
    <div class="table-box">
      <span>{{cronExpression}}</span>
    </div>
  </div>
</template>

<script>
import Time from "./Time";
import day from "./day";
import month from "./month";
import year from "./year";
export default {
  components: {
    Time,
    day,
    month,
    year
  },
  data() {
    return {
      activeName: "s", // el-tab
      // 
      sVal: "0",
      // 
      mVal: "0",
      // 
      hVal: "0",
      // 
      dVal: "*",
      // 
      monthVal: "*",
      // 
      weekVal: "?",
      // 
      yearVal: " ",
      // cron
      cronExpression: ""
    };
  },
  methods: {
    // 
    year(data) {
      this.yearVal = data;
      this.createCron();
    },
    // 
    month(data) {
      this.monthVal = data;
      this.createCron();
    },
    // 
    second(data) {
      this.sVal = data;
      this.createCron();
    },
    // 
    minute(data) {
      this.mVal = data;
      this.createCron();
    },
    // 
    hour(data) {
      this.hVal = data;
      this.createCron();
    },
    // 
    dayAndWeek(data) {
      console.log(data);
      this.dVal = data.day;
      this.weekVal = data.week;
      this.createCron();
    },
    // 
    createCron() {
      this.cronExpression =
        this.sVal +
        " " +
        this.mVal +
        " " +
        this.hVal +
        " " +
        this.dVal +
        " " +
        this.monthVal +
        " " +
        this.weekVal +
        " " +
        this.yearVal;
    }
  },
  created() {
    this.createCron();
  }
};
</script>

<style lang="css">
.cron-box .table-box {
  border-top: 1px solid #ebebeb;
  margin-top: 10px;
  padding-top: 10px;
  min-width: 400px;
}
.cron-box .el-tabs .el-tabs__header .el-tabs__nav-prev {
  display: none;
}
.cron-box .el-tabs .el-tabs__header .el-tabs__nav-next {
  display: none;
}
.cron-box .el-tabs .el-tabs__header .el-tabs__nav-wrap {
  padding: 0 !important;
}
.cron-box .el-tabs .el-tabs__header .el-tabs__nav-scroll {
  width: 400px !important;
}
.cron-box
  .el-tabs
  .el-tabs__header
  .el-tabs__nav-scroll
  .el-tabs__item:first-child {
  margin-left: -1px !important;
}
.cron-box .el-tabs .el-tabs__content {
  overflow-x: hidden;
  overflow-y: auto;
  height: 220px;
}
.cron-box .el-tabs .el-tabs__content::-webkit-scrollbar {
  /**/
  width: 5px; /**/
}
.cron-box .el-tabs .el-tabs__content::-webkit-scrollbar-thumb {
  /**/
  border-radius: 10px;
  background: #ebebeb;
}
.cron-box .el-tabs .el-tabs__content::-webkit-scrollbar-track {
  /**/
  border-radius: 10px;
  background: rgba(0, 0, 0, 0);
}
</style>



